import { Directive,ViewContainerRef,TemplateRef,OnInit } from '@angular/core';

@Directive({
  selector: '[appMyif]'
})
export class MyifDirective {

  constructor(private viewvijay: ViewContainerRef,private tempvijayref:TemplateRef<object>) { }
ngOnInit(){
  // if you false isbool then appMyif will disappeare
 const isbool=false; 
 if (isbool)
 {
   this.viewvijay.createEmbeddedView(this.tempvijayref);
 }
 else
 {
   this.viewvijay.clear();
 }

}
}
