import{Routes,RouterModule} from '@angular/router';
import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { AboutusComponent } from './aboutus/aboutus.component';
import { ContactusComponent } from './contactus/contactus.component';
import { ServicesComponent } from './services/services.component';
import { OurclientsComponent } from './ourclients/ourclients.component';
import { OurheadquoterComponent } from './ourheadquoter/ourheadquoter.component';
import { PostcComponent } from './postc/postc.component';
import { PostComponent } from './post/post.component';




const route:Routes=[
    {path:'demo',component:AppComponent},
    {path:'home',component:HomeComponent},
    {path:'aboutus',children:[
                  {path:"ourclients",component:OurclientsComponent},
                  {path:"ourheadquoter",component:OurheadquoterComponent},
    ]},
    {path:'contactus',component:ContactusComponent},
    {path:'services',component:ServicesComponent},
    {path:'postc',component:PostcComponent},
    {path:'postc/:id',component:PostComponent}
]

@NgModule({
    declarations: [],
    imports: [RouterModule.forRoot(route)],
    exports:[RouterModule],
    providers: [],
  })

export class AppRoutingModule
{


}