import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
// import 'rxjs/add/operator/catch';
// import 'rxjs/add/observable/throw';
import {Mybiodata} from './biodata';
// import {_thorw} from 'rxjs/observable/throw';
import {filter,map,catchError} from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})
export class ServicedataService {

  constructor(private http:HttpClient) { }
  biodata():Observable < Mybiodata[]> {
    return this.http.get<Mybiodata[]> ('../assets/myjson.json')
    .pipe(catchError((res: HttpErrorResponse)=> this.errorcheck(res) ))
  }

  errorcheck(res:HttpErrorResponse)
  {
    const statusCode=res.status;
    const body=res;
    const error={
      statusCode:statusCode,
      error:body.error
    };
    return throwError(body.message);
  }
}

